import {reqAllList} from '@/api'

const state={
    allList:{}
}
const actions={
    //获取正在出售的商品列表
    async getAllList({commit}){
        let result=await reqAllList()
        if(result.code==200){
            commit("GETALLLIST",result.data)
            return 'ok'
        }else{
            return Promise.reject(new Error("failed"))
        }
    }
}
const mutations={
    GETALLLIST(state,allList){
        state.allList=allList
    }
}
const getters={

}


export default {
    namespaced:true,
    state,
    actions,
    mutations,
    getters
}