<template>
  <div>
<el-row :gutter="50" v-if="!list04.length==0"  style="margin-top:30px;max-width:80%;margin:30px auto">

  <el-col :span="6" v-for="(list,index) in list04" :key="list.id" >
    <el-card :body-style="{ padding: '0px', marginBottom: '65px' ,height:'200px'}"     >
          <img v-if="list.goodsImagesList[0]":src="list.goodsImagesList[0]?.image"  style="width:100%;height:100%;margin: 0 auto;display: block;margin-top:3px" class="image multi-content" />
           <img v-else src="../assets/loginLogo.jpg"  style="width:100%;height:100%;margin: 0 auto;display: block;margin-top:3px" class="image multi-content" />
          <div style="padding: 14px;">
            <span style=" font-weight:bold;">{{ list.name }}</span>
            <div class="bottom card-header" style="padding-top:8px;color: #00000080; vertical-align: middle;">
              <span class="time" >{{ ceateTimeList[index]}}</span>

              <el-button type="text" class="button" style="float:right;padding:0" @click="dialogVisible = true">查看详细</el-button>
            </div>
          </div>
    </el-card>
  </el-col>
</el-row>
<el-row :gutter="50"  v-if="!list48.length==0"  style="margin-top:30px;max-width:80% ;margin:30px auto">

 <el-col  v-for="(list,index) in list48" :key="list.id" :span="6"  >
    <el-card :body-style="{ padding: '0px',marginBottom: '65px' ,height:'200px'}"     >
          <img  v-if="list.goodsImagesList[0]" :src="list.goodsImagesList[0]?.image"  style="width:100%;height:100%;margin: 0 auto;display: block;margin-top:3px" class="image multi-content" />
           <img v-else src="../assets/loginLogo.jpg"  style="width:100%;height:100%;margin: 0 auto;display: block;margin-top:3px" class="image multi-content" />
          <div style="padding: 14px;">
            <span style=" font-weight:bold;">{{ list.name }}</span>
            <div class="bottom card-header" style="padding-top:8px;color: #00000080; vertical-align: middle;">
              <span class="time" >{{ ceateTimeList[index]}}</span>

              <el-button type="text" class="button" style="float:right;padding:0" @click="dialogVisible = true">查看详细</el-button>
            </div>
          </div>
    </el-card>
  </el-col>

</el-row>
<el-row :gutter="50" v-if="!list812.length==0" style="margin-top:30px;max-width:80%;margin:30px auto">

  <el-col  :span="6" v-for="(list,index) in list812" :key="list.id" >
    <el-card :body-style="{ padding: '0px',marginBottom: '65px' ,height:'200px'}" >
          <img v-if="list.goodsImagesList[0]" :src="list.goodsImagesList[0]?.image"  style="width:100%;height:100%;margin: 0 auto;display: block;margin-top:3px" class="image multi-content" />
          <img v-else src="../assets/loginLogo.jpg"  style="width:100%;height:100%;margin: 0 auto;display: block;margin-top:3px" class="image multi-content" />
          <div style="padding: 14px;">
            <span style=" font-weight:bold;">{{ list.name }}</span>
            <div class="bottom card-header" style="padding-top:8px;color: #00000080; vertical-align: middle;">
              <span class="time" >{{ ceateTimeList[index]}}</span>

              <el-button type="text" class="button" style="float:right;padding:0" @click="dialogVisible = true">查看详细</el-button>
            </div>
          </div>
    </el-card>
  </el-col>
</el-row>



<!-- pagination -->
<el-pagination
  background
  layout="prev, pager, next"
  :total="totalPage"
  :pageSize="1"
  @current-change="handleCurrentChange"
  style="display: flex;  justify-content: center;margin:0 auto"
  >
</el-pagination>


<!-- dialog -->
<el-dialog
  title="提示"
  :visible.sync="dialogVisible"
  width="30%"
  >
  <span>这是一段信息</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
// import { mapState } from 'vuex'
export default {
    name:'List',
    data() {
        return {
             dialogVisible: false,
             list04:[],
             list48:[],
             list812:[],
            //  listLength:0,
           
             
        }
    },
    watch:{
        allList(newValue){
            // this.listLength=newValue.length
            this.list04=newValue.slice(0,4)
            this.list48=newValue.slice(4,8)
            this.list812=newValue.slice(8,12)
        }
    },
    mounted() {
        this.$store.dispatch("list/getAllList")
        
    },
    computed:{
        // ...mapState("list",["allList"]),
        allList(){
            return this.$store.state.list.allList||[]

        },
        //格式化创建日期
        ceateTimeList(){
           
            let createTimeList=[]
            this.allList.forEach((item,index) => {

                let createTime=item.createTime.split('T')[0]
                
                createTimeList[index]=createTime        
            });
            return createTimeList
        },
        // list04(){
        //     return this.allList.slice(0,4)||[]
        // }
        totalPage(){
            return Math.ceil(this.allList.length/12)
        }
       
    },
    methods: {
         handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      //页数变化
      handleCurrentChange(currentPage){
        this.list04=this.allList.length>(currentPage-1)*12?this.allList.slice((currentPage-1)*12,currentPage*12-8):[]
        this.list48=this.allList.length>(currentPage-1)*12+4?this.allList.slice((currentPage-1)*12+4,currentPage*12-4):[]
        this.list812=this.allList.length>(currentPage-1)*12+8?this.allList.slice((currentPage-1)*12+8,currentPage*12):[]
       
      }
   
}
}
</script>

<style lang="less" scoped>
  .el-row {
    margin-bottom: 20px;
   
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>